.modal {
  height: fit-content;
  max-height: 98%;
  width: 96%;
  max-width: 96%;
  transition: transform var(--timing-function__main), visibility var(--timing-function__main);
  background-color: #fff;
  border-radius: 4px;
  flex-direction: column;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%)scale(1);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

.modal-name {
  text-align: center;
  margin: 0 auto;
  font-size: 18px;
}

.modal-top {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.modal-overflow {
  max-height: 100%;
  min-height: 70%;
  flex-direction: column;
  justify-content: start;
  display: flex;
  overflow: auto;
}

.modal-content {
  padding: 0;
}

.modal .--emptyCartContent {
  text-align: center;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.3;
}

@media screen and (min-width: 500px) {
  .modal {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    height: fit-content;
    max-height: 98%;
    min-width: 480px;
    width: fit-content;
    max-width: 800px;
  }
}

@media screen and (min-width: 1200px) {
  .modal {
    height: fit-content;
    max-height: 98%;
    max-width: 1000px;
  }
}

.backdrop {
  z-index: 1010;
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity var(--timing-function__main), visibility var(--timing-function__main);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.8);
}

.--notScrolled {
  overflow: hidden;
}

.button {
  letter-spacing: .05em;
  text-align: center;
  transition: all var(--timing-function__main);
  border: 1px solid rgba(0, 0, 0, 0);
  line-height: 1.2;
  display: block;
}

.button.--close-modal {
  width: 30px;
  height: 30px;
  fill: #000;
  transition: box-shadow var(--anim-main-timing-function);
  background-color: #fff;
  justify-content: center;
  align-self: flex-start;
  align-items: center;
  display: flex;
}

.button.--close-modal .btn-icon {
  width: 12px;
  height: 12px;
  min-width: 12px;
  min-height: 12px;
}

.button.--close-modal:hover, .button.--close-modal:focus {
  fill: var(--brand_clr__1--accent);
  background-color: rgba(225, 225, 225, .3);
}

.button.--close-modal:active {
  background-color: rgba(225, 225, 225, .6);
}

